				<el-form-item :style='{"width":"100%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else-if="ruleForm.gangweifuli" label="岗位福利" prop="gangweifuli">
					<span :style='{"padding":"0 20px","color":"inherit","background":"none","display":"inline-block","fontSize":"14px","lineHeight":"40px","fontWeight":"500"}'>{{ruleForm.gangweifuli}}</span>
